
.projects-container{
    padding: 60px 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 22px;
}
.projects-container>h3{
    color: #1E1E1E;

/* P Title 65 */
font-family: Poppins;
font-size: 65px;
font-style: normal;
font-weight: 600;
line-height: normal;
letter-spacing: 1.95px;
}
.projects-container>p{
    color: #000;
text-align: center;

/* 21 Regular Lorem */
font-family: Poppins;
font-size: 21px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 0.63px;
}
.projects-container>.project-cards{
    display: flex;
    gap: 48px;
    margin-top: 70px;
    flex-wrap: wrap;
    justify-content: center;
}
.projects-container>.project-cards>.project-card{
    display: flex;
    flex-direction: column;
    gap: 10px;
    position: relative;
    cursor: pointer;
}
.projects-container>.project-cards>.project-card:hover>span{
    background-color: #494444;
}
.projects-container>.project-cards>.project-card:hover>span>img{
    opacity: 0.5;
}
.projects-container>.project-cards>.project-card>a{
    display: none;
}
.projects-container>.project-cards>.project-card:hover>a{
    display: flex;
    position: absolute;
    align-self: center;
    top: 40%;
    width: fit-content;
}
.projects-container>.project-cards>.project-card:hover>a>p{
    color: #FFF;
    font-family: Poppins;
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.57px;
}
.projects-container>.project-cards>.project-card>span{
    width: fit-content;
    height: fit-content;
}
.projects-container>.project-cards>.project-card>span>img{
    width: 445px;
height: 489px;
margin-bottom: 15px;
background-color: #FFEBDB;
object-fit: contain;
}
.projects-container>.project-cards>.project-card>p{
    color: #FD6F00;

/* Sub P 19 */
font-family: Poppins;
font-size: 19px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 0.57px;
}
.projects-container>.project-cards>.project-card>h5{
    color: #000;

/* Sub heading 24 */
font-family: Poppins;
font-size: 24px;
font-style: normal;
font-weight: 700;
line-height: normal;
letter-spacing: 0.72px;
}
@media screen and (max-width:1450px) {
    .projects-container>h3 {
        font-size: 48px;
    }
    .projects-container>p {
        font-size: 18px;
    }
    .projects-container>.project-cards {
        gap: 20px;
    }
    .projects-container>.project-cards>.project-card>span>img {
        width: 345px;
        height: 365px;
    }
    .projects-container>.project-cards>.project-card>h5 {
        font-size: 20px;
    }
    .projects-container>.project-cards>.project-card>p {
        font-size: 15px;
    }
}
@media screen and (max-width:1380px) {
    .projects-container>.project-cards>.project-card>span>img {
        width: 305px;
        height: 325px;
    }
}
@media screen and (max-width:1260px) {
    .projects-container>.project-cards>.project-card>span {
        width: 265px;
        height: 305px;
    }
    .projects-container>.project-cards>.project-card>span>img {
        width: 265px;
        height: 305px;
    }
    .projects-container>.project-cards>.project-card>h5 {
        font-size: 18px;
    }
}
@media screen and (max-width:1164px) {
    .projects-container>.project-cards>.project-card {
        width: 245px;
        height: auto;
    }
    .projects-container>.project-cards>.project-card>span>img {
        width: 245px;
        height: 265px;
    }
}
@media screen and (max-width: 1100px) {
    .projects-container>h3 {
        font-size: 40px;
    }
    .projects-container>.project-cards>.project-card {
        width: 205px;
        height: auto;
    }    
    .projects-container>.project-cards>.project-card>span {
        width: 205px;
        height: 245px;
    }
    .projects-container>.project-cards>.project-card>span>img {
        width: 205px;
        height: 245px;
    }
    .projects-container>.project-cards>.project-card>h5 {
        font-size: 16px;
    }
    .projects-container>.project-cards>.project-card>p {
        font-size: 13px;
    }
}
@media screen and (max-width:1000px) {
    .projects-container {
        padding: 30px 70px;
    }
}
@media screen and (max-width: 800px) {
    .projects-container {
        padding: 30px 50px;
    }
    .projects-container>h3 {
        font-size: 36px;
    }
    .projects-container>.project-cards {
        margin-top: 30px;
        width: 100%;
        overflow-x: scroll;
        flex-wrap: nowrap;
    }
}
@media screen and (max-width: 780px) {
    
    .projects-container>.project-cards {
        justify-content: flex-start;
    }
}
@media screen and (max-width: 600px) {
    .projects-container {
        padding: 20px;
    }
}
@media screen and (max-width: 480px) {
    .projects-container>h3 {
        font-size: 30px;
    }
    .projects-container>p {
        font-size: 16px;
    }
}