.navbar-container{
    padding: 60px 150px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
}
.navbar-container>h2{
    color:  #545454;
font-family: Montserrat;
font-size: 48px;
font-style: normal;
font-weight: 400;
line-height: normal;
}
.navbar-container>h2>span{
    color: var(--charcole-black, #1E1E1E);
font-family: Montserrat;
font-size: 48px;
font-style: normal;
font-weight: 700;
line-height: normal;
}
.navbar-container>.navbar-list-items{
    display: flex;
    gap: 40px;
    align-items: center;
}
.navbar-container>.navbar-list-items>li{
    color: #000;
font-family: Poppins;
font-size: 21px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 0.63px;
list-style-type: none;
cursor: pointer;
}
.navbar-container>.navbar-list-items>button{
    border: none;
    height: fit-content;
    border-radius: 5px;
background: #FD6F00;
color: #FFF;
padding: 8px 18px;
font-family: Poppins;
font-size: 21px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 0.63px;
cursor: pointer;
}
.navbar-container>.navbar-icon{
    display: none;
    cursor: pointer;
    position: relative;
}
.navbar-container>.navbar-icon>span{
    position: absolute;
    width: 30px;
    height: 5px;
    border-radius: 5px;
    background-color: #000;
    transition: 0.5s;
}
.navbar-container>.navbar-icon>span:nth-child(1){
    top: 5px;
}
.navbar-container>.navbar-icon>span:nth-child(2){
    bottom: 5px;
}
.navbar-container>.navbar-icon.active>span:nth-child(1){
    top: 13px;
    transform: rotate(45deg);
    transition: 0.5s;
}
.navbar-container>.navbar-icon.active>span:nth-child(2){
    bottom: 12px;
    transform: rotate(-45deg);
    transition: 0.5s;
}

@media screen and (max-width:1450px) {
    .navbar-container{
        padding: 60px 100px;
    }
    .navbar-container>h2 {
        font-size: 40px;
    }
    .navbar-container>.navbar-list-items {
        gap: 30px;
    }
    .navbar-container>.navbar-list-items>li {
        font-size: 18px;
    }
    .navbar-container>.navbar-list-items>button {
        font-size: 18px;
        padding: 5px 12px;
    }
}
@media screen and (max-width:1000px) {
    .navbar-container {
        padding: 30px 70px;
    }
    .navbar-container>.navbar-list-items {
        gap: 15px;
    }
}
@media screen and (max-width:780px) {
    .navbar-container {
        padding: 30px 50px;
    }
    .navbar-container>h2 {
        font-size: 32px;
    }
    .navbar-container>.navbar-list-items>li {
        font-size: 16px;
    }
    .navbar-container>.navbar-list-items>button {
        font-size: 16px;
        padding: 5px 10px;
    }
}
@media screen and (max-width:720px) {
    .navbar-container>.navbar-list-items{
        display: none;
    }
    .navbar-container>.navbar-icon{
        display: flex;
        flex-direction: column;
        gap: 10px;
        align-items: center;
        justify-content: center;
        width: 30px;
        height: 30px;
    }
}
@media screen and (max-width:580px) {
    
    .navbar-container {
        padding: 20px;
    }
}