.aboutMe-container{
    padding: 60px 150px;
    display: flex;
    gap: 100px;
    align-items: center;
}
.aboutMe-container>.aboutMe-image{
    width: 50%;
}
.aboutMe-container>.aboutMe-image>span{
    display: block;
    width: 545px;
height: 545px;
}
.aboutMe-container>.aboutMe-image>span>img{
    width: 545px;
height: 545px;
}
.aboutMe-container>.aboutMe-content{
    display: flex;
    flex-direction: column;
    gap: 18px;
}
.aboutMe-container>.aboutMe-content>h2{
    color: #000;

/* H2 65 */
font-family: Poppins;
font-size: 65px;
font-style: normal;
font-weight: 600;
line-height: normal;
letter-spacing: 1.95px;
}
.aboutMe-container>.aboutMe-content>p{
    color: #000;
font-family: Poppins;
font-size: 21px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 0.63px;
}
.aboutMe-container>.aboutMe-content>h4{
    color: #000;
    font-family: Poppins;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: 0.72px;
}
.aboutMe-container>.aboutMe-content>.aboutMe-content-highlights{
    display: flex;
    flex-direction: column;
    gap: 15px;
}
.aboutMe-container>.aboutMe-content>.aboutMe-content-highlights>li{
    list-style-type: none;
    color: #545454;
font-family: Poppins;
font-size: 18px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 0.63px;
margin-left: 30px;
}
.aboutMe-container>.aboutMe-content>.aboutMe-content-highlights>li::before{
    content: "";
    position: absolute;
    width: 16px;
    height: 16px;
    border-radius: 16px;
    background-color: #FF8233;
    margin-left: -30px;
    margin-top: 5px;
}
@media screen and (max-width:1450px) {
    .aboutMe-container>.aboutMe-image>span {
        
        width: 400px;
        height: 400px;
    }
    .aboutMe-container>.aboutMe-image>span>img {
        width: 400px;
        height: 400px;
    }
    .aboutMe-container>.aboutMe-content>h2 {
        font-size: 48px;
    }
    .aboutMe-container>.aboutMe-content>p {
        font-size: 18px;
    }
    .aboutMe-container>.aboutMe-content>.aboutMe-content-highlights {
        gap: 10px;
    }
}
@media screen and (max-width:1250px) {
    .aboutMe-container {
        gap: 50px;
    }
    .aboutMe-container>.aboutMe-content>p {
        font-size: 16px;
    }
    .aboutMe-container>.aboutMe-content>.aboutMe-content-highlights {
        gap: 5px;
        width: 120%;
    }
}
@media screen and (max-width:1100px) {
    .aboutMe-container>.aboutMe-content {
        gap: 12px;
    }
    .aboutMe-container>.aboutMe-content>h2 {
        font-size: 40px;
    }
    .aboutMe-container>.aboutMe-image>span {
        width: 350px;
        height: 350px;
    }
    .aboutMe-container>.aboutMe-image>span>img {
        width: 350px;
        height: 350px;
    }
    .aboutMe-container>.aboutMe-content>p {
        font-size: 14px;
    }
    .aboutMe-container>.aboutMe-content>h4 {
        font-size: 20px;
    }
    .aboutMe-container>.aboutMe-content>.aboutMe-content-highlights>li {
        font-size: 16px;
    }
}
@media screen and (max-width:1000px) {
    .aboutMe-container {
        padding: 30px 70px;
    }
    .aboutMe-container>.aboutMe-content>.aboutMe-content-highlights {
        gap: 5px;
        width: 100%;
    }
}
@media screen and (max-width: 780px) {
    .aboutMe-container {
        padding: 30px 50px;
    }
    .aboutMe-container>.aboutMe-content {
        gap: 9px;
    }
    .aboutMe-container>.aboutMe-content>h2 {
        font-size: 36px;
    }
    .aboutMe-container>.aboutMe-content>h4 {
        font-size: 18px;
    }
    .aboutMe-container>.aboutMe-content>.aboutMe-content-highlights>li {
        font-size: 14px;
    }
    .aboutMe-container>.aboutMe-content>.aboutMe-content-highlights>li::before {
        width: 12px;
        height: 12px;
        border-radius: 12px;
        margin-left: -20px;
    }
    .aboutMe-container>.aboutMe-content>p {
        font-size: 12px;
    }
    .aboutMe-container>.aboutMe-image>span {
        width: 300px;
        height: 300px;
    }
    .aboutMe-container>.aboutMe-image>span>img {
        width: 300px;
        height: 300px;
    }

}
@media screen and (max-width:720px) {
    .aboutMe-container {
        gap: 20px;
    }
    .aboutMe-container>.aboutMe-image>span {
        width: 230px;
        height: 230px;
    }
    .aboutMe-container>.aboutMe-image>span>img {
        width: 230px;
        height: 230px;
    }
    .aboutMe-container>.aboutMe-content>h4 {
        font-size: 16px;
    }
    .aboutMe-container>.aboutMe-content>.aboutMe-content-highlights>li::before {
        margin-top: 2px;
    }
    .aboutMe-container>.aboutMe-content>.aboutMe-content-highlights>li {
        font-size: 12px;
    }
}
@media screen and (max-width: 600px) {
    .aboutMe-container>.aboutMe-image {
        display: none;
    }
    .aboutMe-container {
        padding: 20px;
    }
    .aboutMe-container>.aboutMe-content {
        align-items: center;
    }
    .aboutMe-container>.aboutMe-content>h2 {
        text-align: center;
    }
    .aboutMe-container>.aboutMe-content>p {
        font-size: 14px;
        width: 80%;
        text-align: center;
    }
    .aboutMe-container>.aboutMe-content>h4 {
        font-size: 22px;
        margin-top: 20px;
    }
    .aboutMe-container>.aboutMe-content>.aboutMe-content-highlights {
        width: 80%;
    }
    .aboutMe-container>.aboutMe-content>.aboutMe-content-highlights>li {
        font-size: 16px;
    }
    .aboutMe-container>.aboutMe-content>.aboutMe-content-highlights>li::before {
        margin-top: 6px;
    }
    
}
@media screen and (max-width: 480px) {
    .aboutMe-container>.aboutMe-content>h2 {
        font-size: 30px;
    }
    
    .aboutMe-container>.aboutMe-content>p {
        width: 90%;
    }
    .aboutMe-container>.aboutMe-content>h4 {
        font-size: 20px;
        margin-top: 10px;
    }
    .aboutMe-container>.aboutMe-content>.aboutMe-content-highlights {
        width: 100%;
    }
    .aboutMe-container>.aboutMe-content>.aboutMe-content-highlights>li {
        font-size: 14px;
    }
}
@media screen and (max-width: 360px) {
    .aboutMe-container>.aboutMe-content>p {
        font-size: 12px;
    }
    .aboutMe-container>.aboutMe-content>h4 {
        font-size: 18px;
    }
    .aboutMe-container>.aboutMe-content>.aboutMe-content-highlights>li {
        font-size: 12px;
    }
}