.sidebar-container{
    display: none;
    position: fixed;
    top: 0;
    left: -350px;
    background-color: #FFF;
    z-index: 10;
    width: 350px;
    height: 100%;
    transition: 0.5s;
}
.sidebar-container.open{
    left: 0;
    transition: 0.5s;
}
.sidebar-container>h2{
    
    padding: 20px 50px;
    color: #FFF;
    font-family: Montserrat;
    font-size: 48px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    background: #FD6F00;
}
.sidebar-container>h2>span{
    
    color: #FFF;
    font-family: Montserrat;
    font-size: 48px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
}
.sidebar-container>.sidebar-list-items{
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding: 20px 70px;
    height: 80%;
}
.sidebar-container>.sidebar-list-items>li{
    
    color: #000;
    font-family: Poppins;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.63px;
    list-style-type: none;
    cursor: pointer;
}
.sidebar-container>.sidebar-list-items>li:nth-child(5){
    margin-bottom: auto;
}
.sidebar-container>.sidebar-list-items>a{
    width: fit-content;
}
.sidebar-container>.sidebar-list-items>a>button{
    
    border: none;
    height: fit-content;
    border-radius: 5px;
    background: #FD6F00;
    color: #FFF;
    padding: 5px 10px;
    font-family: Poppins;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.63px;
    cursor: pointer;
    width: fit-content;
}
@media screen and (max-width:720px) {
    .sidebar-container{
        display: block;
        width: 300px;
    }
}
@media screen and (max-width:400px) {
    .sidebar-container{
        width: 250px;
    }
    .sidebar-container>.sidebar-list-items {
        padding: 20px 50px;
    }
}