.contactInfo-container{
    padding: 60px 150px;
    display: flex;
    flex-direction: column;
    gap: 30px;
    background: #F8F8F8;
    align-items: center;
}
.contactInfo-container>h2{
    
    color: #1E1E1E;

/* P Title 65 */
font-family: Poppins;
font-size: 65px;
font-style: normal;
font-weight: 600;
line-height: normal;
letter-spacing: 1.95px;
}
.contactInfo-container>.contactInfo-cards{
    display: flex;
    gap: 50px;
}
.contactInfo-container>.contactInfo-cards>.contactInfo-card{
    display: flex;
    flex-direction: column;
    gap: 32px;
    padding: 30px;
    background-color: #FFF;
    align-items: center;
    border-radius: 18px;
flex-shrink: 0;
}
.contactInfo-container>.contactInfo-cards>.contactInfo-card>span:nth-child(1){
    width: 80px;
    height: 80px;
    border-radius: 80px;
    background-color: #FD6F00;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
}
.contactInfo-container>.contactInfo-cards>.contactInfo-card>span:nth-child(1)>svg{
    width: 36px;
height: 36px;
flex-shrink: 0;
}
.contactInfo-container>.contactInfo-cards>.contactInfo-card>span{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    gap: 16px;
}
.contactInfo-container>.contactInfo-cards>.contactInfo-card>span>p:nth-child(1){
    
    color: #000;
    font-family: Poppins;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: 0.72px;
}
.contactInfo-container>.contactInfo-cards>.contactInfo-card>span>p:nth-child(2){
  
    color: #545454;
    font-family: Poppins;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.63px;
}
.contactInfo-container>.social-media-icons{
    width: 80%;
    display: flex;
    gap: 20px;
    justify-content: center;
}
.contactInfo-container>.social-media-icons>svg,
.contactInfo-container>.social-media-icons>a>svg{
    width: 32px;
    height: 32px;
    cursor: pointer;
}
@media screen and (max-width:1450px) {
    .contactInfo-container>h2 {
        font-size: 48px;
    }
    .contactInfo-container>.social-media-icons>svg,
    .contactInfo-container>.social-media-icons>a>svg{
        width: 24px;
        height: 24px;
    }
}
@media screen and (max-width:1400px) {
    
    .contactInfo-container>.contactInfo-cards>.contactInfo-card {
        gap: 16px;
    }
    .contactInfo-container>.contactInfo-cards>.contactInfo-card>span:nth-child(1) {
        width: 60px;
        height: 60px;
        padding: 10px;
    }
    .contactInfo-container>.contactInfo-cards>.contactInfo-card>span:nth-child(1)>svg {
        width: 24px;
        height: 24px;
    }
    .contactInfo-container>.contactInfo-cards>.contactInfo-card>span>p:nth-child(1) {
        font-size: 20px;
    }
    .contactInfo-container>.contactInfo-cards>.contactInfo-card>span>p:nth-child(2) {
        font-size: 14px;
    }
}
@media screen and (max-width: 1150px) {
    .contactInfo-container>.contactInfo-cards>.contactInfo-card {
        padding: 15px;
    }
}
@media screen and (max-width: 1100px) {
    .contactInfo-container>h2 {
        font-size: 40px;
    }
}
@media screen and (max-width:1000px) {
    .contactInfo-container {
        padding: 30px 70px;
    }
}
@media screen and (max-width: 900px) {
    .contactInfo-container>.contactInfo-cards {
        gap: 25px;
    }
}
@media screen and (max-width: 780px) {
    .contactInfo-container {
        padding: 30px 50px;
    }
    .contactInfo-container>h2 {
        font-size: 36px;
    }
    .contactInfo-container>.contactInfo-cards {
        gap: 20px;
        width: 100%;
        overflow-x: scroll;
    }
    .contactInfo-container>.contactInfo-cards>.contactInfo-card>span>p:nth-child(1) {
        font-size: 18px;
    }
}
@media screen and (max-width: 780px) {
    .contactInfo-container {
        padding: 20px;
    }
}
@media screen and (max-width: 480px) {
    .contactInfo-container>h2 {
        font-size: 30px;
    }
    .contactInfo-container>.social-media-icons>svg,
    .contactInfo-container>.social-media-icons>a>svg {
         width: 18px;
         height: 18px;
    }
    .contactInfo-container>.contactInfo-cards>.contactInfo-card>span>p:nth-child(1) {
        font-size: 16px;
    }
    .contactInfo-container>.contactInfo-cards>.contactInfo-card>span>p:nth-child(2) {
        font-size: 12px;
    }
    .contactInfo-container>.contactInfo-cards {
        flex-direction: column;
        width: auto;
    }
}
@media screen and (max-width: 350px) {
    .contactInfo-container>.contactInfo-cards>.contactInfo-card>span {
        gap: 10px;
    }
    .contactInfo-container>.contactInfo-cards>.contactInfo-card>span>p:nth-child(1) {
        font-size: 14px;
    }
}