.home-container{
    padding: 60px 150px;
    display: flex;
    gap: 100px;
}
.home-container>.home-content{
    width: 50%;
    display: flex;
    flex-direction: column;
    gap: 12px;
}
.home-container>.home-content>p:nth-child(1){
    color: #000;
font-family: Poppins;
font-size: 24px;
font-style: normal;
font-weight: 600;
line-height: normal;
letter-spacing: 0.72px;
}
.home-container>.home-content>p:nth-child(2){
    color: #FD6F00;
font-family: Poppins;
font-size: 32px;
font-style: normal;
font-weight: 600;
line-height: normal;
letter-spacing: 0.96px;
}
.home-container>.home-content>h2:nth-child(3){
    color: #000;

/* Main heading H1 */
font-family: Poppins;
font-size: 100px;
font-style: normal;
font-weight: 700;
line-height: 120%; /* 120px */
letter-spacing: 3px;
align-self: flex-start;
}
.home-container>.home-content>h2:nth-child(4){
    color: #000;
font-family: Poppins;
font-size: 100px;
font-style: normal;
font-weight: 700;
line-height: 120%; /* 120px */
letter-spacing: 3px;
align-self: flex-end;
}
.home-container>.home-content>p:nth-child(5){
    color: #000;
/* Nav Bar text 21 */
font-family: Poppins;
font-size: 21px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 0.63px;
}
.home-container>.home-content>a{
    width: fit-content;
}
.home-container>.home-content>a>button{
    border-radius: 5px;
background: #FD6F00;
color: #FFF;
text-align: justify;
border: none;
padding: 17px 50px;
font-family: Poppins;
font-size: 21px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 0.63px;
width: fit-content;
cursor: pointer;
height: fit-content;
}
.home-container>.home-image{
    width: 50%;
    display: flex;
    flex-direction: column;
    gap: 36px;
    align-items: center;
}
.home-container>.home-image>span{
    display: block;
    width: 485.868px;
height: 485.868px;
    border-radius: 485.868px;
    overflow: hidden;
border: 6px solid #FF8233;
box-shadow: 0px 6px 6px 0px rgba(0, 0, 0, 0.47) inset;
}
.home-container>.home-image>span>img {
    width: 485.868px;
    height: 525.868px;
    transform: translate(-5px, -50px);
}
.home-container>.home-image>.social-media-icons{
    display: flex;
    gap: 16px;
}
.home-container>.home-image>.social-media-icons>svg,
.home-container>.home-image>.social-media-icons>a>svg{
    width: 32px;
    height: 32px;
    cursor: pointer;
}
@media screen and (max-width:1450px) {
    .home-container {
        padding: 60px 100px;
    }
    .home-container>.home-content>h2:nth-child(3) {
        font-size: 64px;
    }
    .home-container>.home-content>h2:nth-child(4) {
        font-size: 64px;
    }
    .home-container>.home-content>a>button {
        padding: 10px 15px;
        font-size: 18px;
    }
    .home-container>.home-content>p:nth-child(5) {
        font-size: 18px;
        width: 80%;
        margin-bottom: 30px;
    }
    .home-container>.home-image>span {
        width: 350px;
        height: 350px;
        border-radius: 350px;
    }
    .home-container>.home-image {
        gap: 20px;
    }
  
    .home-container>.home-image>span>img {
        transform: translate(-5px, -50px);
        width: 350px;
        height: 400px;
    }
    .home-container>.home-image>.social-media-icons>svg,
.home-container>.home-image>.social-media-icons>a>svg {
        width: 24px;
        height: 24px;
    }
}
@media screen and (max-width:1100px) {
    .home-container>.home-image>span {
        width: 300px;
        height: 300px;
        border-radius: 300px;
    }
    .home-container>.home-image>span>img {
        width: 300px;
        height: 350px;
    }
    .home-container>.home-content>h2:nth-child(3) {
        font-size: 58px;
    }
    .home-container>.home-content>h2:nth-child(4) {
        font-size: 58px;
    }
    .home-container>.home-content>p:nth-child(5) {
        width: 100%;
    }
}
@media screen and (max-width:1000px) {
    .home-container {
        padding: 30px 70px;
        gap: 50px;
    }
    .home-container>.home-content>p:nth-child(1) {
        font-size: 20px;
    }
    .home-container>.home-content>p:nth-child(2) {
        font-size: 24px;
    }
    .home-container>.home-content>h2:nth-child(3) {
        font-size: 48px;
    }
    .home-container>.home-content>h2:nth-child(4) {
        font-size: 48px;
    }
    .home-container>.home-content>a>button {
        padding: 5px 10px;
        font-size: 16px;
    }
}
@media screen and (max-width:850px) {
    .home-container>.home-image>span {
        width: 250px;
        height: 250px;
        border-radius: 250px;
    }
    .home-container>.home-image>span>img {
        width: 250px;
        height: 300px;
    }
}
@media screen and (max-width:780px) {
    .home-container {
        padding: 30px 50px;
    }
    .home-container>.home-content>p:nth-child(5) {
        margin-bottom: 10px;
    }
    .home-container>.home-content {
        gap: 9px;
    }
    .home-container>.home-content>p:nth-child(1) {
        font-size: 18px;
    }
    .home-container>.home-content>p:nth-child(2) {
        font-size: 20px;
    }
    .home-container>.home-content>h2:nth-child(3) {
        font-size: 36px;
    }
    .home-container>.home-content>h2:nth-child(4) {
        font-size: 36px;
    }
    .home-container>.home-content>p:nth-child(5) {
        font-size: 16px;
    }
}
@media screen and (max-width:720px) {
    
    .home-container {
        gap: 20px;
    }
    .home-container>.home-image>span>img {
        width: 230px;
        height: 260px;
        transform: translate(-5px, -42px);
    }
    .home-container>.home-image>span {
        width: 230px;
        height: 230px;
        border-radius: 230px;
    }

}
@media screen and (max-width: 580px) {
    .home-container {
        padding: 20px;
    }
    
    .home-container>.home-content {
        width: 60%;
    }
    .home-container>.home-image {
        width: 40%;
    }
    .home-container>.home-image>span {
        width: 230px;
        height: 230px;
        border-radius: 230px;
    }
}
@media screen and (max-width: 490px) {
    .home-container>.home-image>span {
        width: 200px;
        height: 200px;
        border-radius: 200px;
    }
    .home-container>.home-image>span>img {
        width: 200px;
        height: 240px;
        transform: translate(-5px, -39px);
    }
    .home-container>.home-image>.social-media-icons>svg,
.home-container>.home-image>.social-media-icons>a>svg {
        width: 18px;
        height: 18px;
    }
    .home-container>.home-image>.social-media-icons {
        gap: 10px;
    }
    .home-container>.home-content>p:nth-child(5) {
        font-size: 14px;
        width: 120%;
    }
    .home-container>.home-content>a>button {
        padding: 5px ;
        font-size: 14px;
    }
}
@media screen and (max-width: 460px) {
    .home-container>.home-image>span {
        width: 150px;
        height: 150px;
        border-radius: 150px;
    }
    .home-container>.home-image>span>img {
        width: 150px;
        height: 200px;
    }
}
@media screen and (max-width: 420px){
    .home-container>.home-content>p:nth-child(1) {
        font-size: 14px;
    }
    .home-container>.home-content>p:nth-child(2) {
        font-size: 16px;
    }
    .home-container>.home-content>h2:nth-child(3) {
        font-size: 28px;
    }
    .home-container>.home-content>h2:nth-child(4) {
        font-size: 28px;
    }
    .home-container>.home-content>p:nth-child(5) {
        font-size: 12px;
    }
}
@media screen and (max-width: 370px) {
    .home-container>.home-image>span>img {
        width: 140px;
        height: 180px;
    }
    .home-container>.home-image>span {
        width: 140px;
        height: 140px;
        border-radius: 140px;
    }
}
@media screen and (max-width: 350px) {
    
    .home-container {
        gap: 10px;
    }
    .home-container>.home-image {
        gap: 30px;
    }
    .home-container>.home-content>h2:nth-child(3) {
        font-size: 26px;
    }
    .home-container>.home-content>h2:nth-child(4) {
        font-size: 26px;
    }
}